import React from "react";
import LazyLoad from "react-lazyload";
// import "react-lazy-load-image-component/src/effects/blur.css";
import "../css/Mainpage.css";

function Mainpage() {
  const images = [
    "photo1.jpg",
    "photo2.jpg",
    "photo3.jpg",
    "photo4.jpg",
    "photo5.jpg",
    "photo6.jpg",
    "photo7.jpg",
    "photo8.jpg",
    "photo9.jpg",
    "photo10.jpg",
    "photo11.jpg",
    "photo12.jpg",
    "photo13.jpg",
    "photo14.jpg",
    "photo15.jpg",
    "photo16.jpg",
    "photo17.jpg",
    "photo18.jpg",
    "photo19.jpg",
    "photo20.jpg",
    "photo21.jpg",
    "photo22.jpg",
    "photo23.jpg",
    "photo24.jpg",
    "photo25.jpg",
    "photo26.jpg",
    "photo27.jpg",
    "photo28.jpg",
    "photo29.jpg",
    "photo30.jpg",
    "photo31.jpg",
    "photo32.jpg",
  ];

  return (
    <div className="mainpage">
      this is test
      {images.map((image, index) => (
        <LazyLoad height={200} offset={150}>
          <img
            src={`/portfolio/assets/${image}`}
            alt={image}
            // effect="blur"
            // placeholderSrc={`/assets/low-res/${image}`} //   
          />
          {index < images.length - 1 && <hr />}
        </LazyLoad>
      ))}
    </div>
  );
}

export default Mainpage;
